import React, { useState } from "react";
import { ReportsDaily } from "./ReportsDaily";
import { ReportsUser } from "./ReportsUser";

export const Reports = () => {
  const [selectedTab, setSelectedTab] = useState<"user" | "daily">("user");
  return (
    <div>
      <div
        style={{
          top: 0,
          left: "1em",
          right: "1em",
          backgroundColor: "#2C3243",
          position: "sticky",
          display: "flex",
          padding: "0 2em",
          justifyContent: "space-around",
          alignItems: "center",
          zIndex: 10,
        }}
      >
        <div
          style={{
            padding: "1em",
            color: "white",
            cursor: "pointer",
            backgroundColor: selectedTab === "daily" ? "#7D9FB2" : undefined,
            flex: 1,
            justifyContent: "center",
            alignItems: "center",
            textAlign: "center",
          }}
          onClick={() => setSelectedTab("daily")}
        >
          User Reports
        </div>
        <div
          style={{
            padding: "1em",
            color: "white",
            cursor: "pointer",
            flex: 1,
            justifyContent: "center",
            alignItems: "center",
            textAlign: "center",
            backgroundColor: selectedTab === "user" ? "#7D9FB2" : undefined,
          }}
          onClick={() => setSelectedTab("user")}
        >
          Daily Reports
        </div>
      </div>
      <div style={{ padding: "4em" }}>
        {selectedTab !== "user" ? <ReportsUser /> : <ReportsDaily />}
      </div>
    </div>
  );
};
