import { useQuery } from "react-query";
import { CACHE_TIME } from ".";
import { axios, getToken } from "../api";

export const useUserReports = () => {
  return useQuery(
    "user reports",
    () => {
      return axios
        .get("/api/reports/users", {
          headers: {
            authorization: "Bearer " + getToken(),
          },
        })
        .then(
          (data) =>
            data.data as {
              calls: {
                total: number;
                moreThan30s: number;
                missed: number;
              };
              firstName: string;
              lastName: string;
              id: number;
            }[]
        );
    },
    { cacheTime: CACHE_TIME, staleTime: CACHE_TIME }
  );
};

export const useDailyReports = () => {
  return useQuery(
    "daily reports",
    () => {
      return axios
        .get("/api/reports/daily", {
          headers: {
            authorization: "Bearer " + getToken(),
          },
        })
        .then(
          (data) =>
            data.data as {
              date: string;
              incoming: number;
              missed: number;
              outgoing: number;
            }[]
        );
    },
    { cacheTime: CACHE_TIME, staleTime: CACHE_TIME }
  );
};
