import React from "react";
import { useHistory } from "react-router-dom";
import Delete from "../../images/delete.png";
import Setting from "../../images/settings.png";

export const TeamCard = ({ name, count, email, onDelete, id }: Props) => {
  const history = useHistory();

  return (
    <div
      style={{
        width: "25%",
        maxWidth: "22%",
        flex: 1,
        padding: "0.5em",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        alignItems: "center",
        borderRadius: 8,
        cursor: "pointer",
        background: "linear-gradient(90deg,#813866 0%,#ff9d82 100%)",
      }}
      onClick={() => history.push("/teams/" + id)}
    >
      <h3>{name}</h3>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-around",
          width: "100%",
        }}
      >
        <div
          style={{
            padding: "0.3em 0.7em",
            fontSize: 24,
            color: "white",
            backgroundColor: "#C1497B",
            borderRadius: 30,
            border: "2px solid white",
          }}
        >
          {count}
        </div>
        <div
          style={{
            backgroundColor: "white",
            padding: "0 0.6em",
            fontWeight: "bold",
          }}
        >
          {email ?? "support@gmail.com"}
        </div>
      </div>
      <div
        style={{ display: "flex", flexDirection: "row-reverse", width: "100%" }}
      >
        <div
          style={{
            padding: "0.5em 0",
            marginLeft: "auto",
            marginRight: "0.5em",
          }}
        >
          <img
            src={Setting}
            alt="Setting"
            style={{
              objectFit: "contain",
              maxHeight: 25,
              borderRadius: "50%",
              marginRight: "0.3em",
            }}
          />
          <img
            src={Delete}
            alt="Delete"
            onClick={() => {
              const doDelete = window.confirm(
                `Do you want to delete ${name} team?`
              );
              if (doDelete && onDelete) {
                onDelete();
              }
            }}
            style={{
              objectFit: "contain",
              maxHeight: 25,
              borderRadius: "50%",
              cursor: "pointer",
            }}
          />
        </div>
      </div>
    </div>
  );
};

interface Props {
  name: string;
  count: number;
  email?: string;
  onDelete: () => void;
  id: number;
}
