import React, { useEffect, useMemo, useRef } from "react";
import AfterCallUser from "./components/AfterCallUser";
import { Card } from "./components/Card/Card";
import FrwdToDevice from "./components/FrwdToDevice";
import TimeZoneUser from "./components/TimeZoneUser";
import EC from "./images/EC.png";
import Mail from "./images/mail.png";
import Call from "./images/call.png";
import SMS from "./images/sms.png";
import { Phonenumber } from "./utils/types";
import { useState } from "react";
import { useParams } from "react-router-dom";
import { useAssignUserNumber, useUser } from "./utils/hooks/useUser";
import { usePhoneNumbers } from "./utils/hooks/usePhoneNumbers";
import { Spinner } from "react-activity";
import { compareNumberArray } from "./utils/general";
import Button from "./components/Button";

export const PhoneCard = ({
  data,
  index,
  selected,
  hasTeam,
  onClick,
}: {
  data: Phonenumber;
  index: number;
  hasTeam: boolean;
  selected?: boolean;
  onClick?: () => void;
}) => {
  return (
    <Card.Body
      onClick={hasTeam ? undefined : onClick}
      style={{
        borderRadius: "0.5em",
        padding: "1em 2em",
        cursor: hasTeam ? "not-allowed" : "pointer",
        opacity: hasTeam ? 0.5 : 1,
        border: selected ? "2px solid black" : "2px solid white",
      }}
      background={index % 2 === 0 ? "gradient" : "orange"}
    >
      <h3>{data.name}</h3>
      <p>{hasTeam ? "Assigned to team" : data.number}</p>
    </Card.Body>
  );
};

export default function User() {
  const [selectedNumber, setSelectedNumber] = useState<Phonenumber["id"][]>([]);
  const ref = useRef<HTMLDivElement>(null);
  const availRef = useRef<HTMLDivElement>(null);
  const allocateRef = useRef<HTMLDivElement>(null);
  const afterCallRef = useRef<HTMLDivElement>(null);
  const smsRef = useRef<HTMLDivElement>(null);
  const userRef = useRef<HTMLDivElement>(null);
  const { id }: { id: string } = useParams();
  const { data: numbers, isLoading: numbersLoading } = usePhoneNumbers();
  const { data: user, isLoading } = useUser(id);

  useEffect(() => {
    if (id === "new") {
      return;
    }

    if (!user || !numbers) {
      return;
    }

    const userNumbers = user.phoneNumbers.map(({ id }) => id);

    setSelectedNumber(
      numbers.filter(({ id }) => userNumbers.includes(id)).map(({ id }) => id)
    );
  }, [user, numbers, id]);

  const isChanged = useMemo(() => {
    const userNumbers = user?.phoneNumbers.map(({ id }) => id) ?? [];

    return !compareNumberArray(userNumbers, selectedNumber);
  }, [user, selectedNumber]);

  const { mutate, isLoading: mutating } = useAssignUserNumber(id);

  // Dont know Dont care.
  if (id === "new") {
    return null;
  }

  if (!user || isLoading || numbersLoading) {
    return <Spinner />;
  }

  return (
    <div>
      <div
        style={{
          top: 0,
          left: "1em",
          right: "1em",
          backgroundColor: "#2C3243",
          position: "sticky",
          display: "flex",
          padding: "0.5em 2em",
          justifyContent: "space-around",
          alignItems: "center",
          zIndex: 10,
        }}
      >
        <div
          style={{ padding: "1em", color: "white", cursor: "pointer" }}
          onClick={() =>
            userRef.current?.scrollIntoView({ behavior: "smooth" })
          }
        >
          User
        </div>
        <div
          style={{ padding: "1em", color: "white", cursor: "pointer" }}
          onClick={() => ref.current?.scrollIntoView({ behavior: "smooth" })}
        >
          Forward to Device
        </div>
        <div
          style={{ padding: "1em", color: "white", cursor: "pointer" }}
          onClick={() =>
            availRef.current?.scrollIntoView({ behavior: "smooth" })
          }
        >
          Availability
        </div>
        <div
          style={{ padding: "1em", color: "white", cursor: "pointer" }}
          onClick={() =>
            allocateRef.current?.scrollIntoView({ behavior: "smooth" })
          }
        >
          Allocate Number
        </div>
        <div
          style={{ padding: "1em", color: "white", cursor: "pointer" }}
          onClick={() =>
            afterCallRef.current?.scrollIntoView({ behavior: "smooth" })
          }
        >
          After Call
        </div>
        <div
          style={{ padding: "1em", color: "white", cursor: "pointer" }}
          onClick={() => smsRef.current?.scrollIntoView({ behavior: "smooth" })}
        >
          SMS
        </div>
      </div>
      <section style={{ minHeight: "80vh", paddingTop: "10vh" }} ref={userRef}>
        <div
          style={{
            backgroundColor: "white",
            borderRadius: "1em",
            padding: "1.5em",
            margin: "2em",
          }}
        >
          <h4>User Name and PhoneNumber</h4>
          <div style={{ display: "flex", margin: "1em", gap: "1.5em" }}>
            <Card.Body
              style={{ padding: "1em", flex: 1, borderRadius: "0.5em" }}
              background="dark"
            >
              <div
                style={{ display: "flex", gap: "1em", alignItems: "center" }}
              >
                <img
                  src={EC}
                  alt=""
                  style={{ objectFit: "contain", borderRadius: "100%" }}
                />
                <div>
                  <h5 style={{ color: "white" }}>{`${user.firstName} ${
                    user.lastName ?? ""
                  }`}</h5>
                  <p style={{ color: "white", marginTop: "1em" }}>
                    Employee full name
                  </p>
                </div>
              </div>
            </Card.Body>
            <Card.Body
              style={{ padding: "1em", flex: 1, borderRadius: "0.5em" }}
              background="orange"
            >
              <div
                style={{ display: "flex", gap: "1em", alignItems: "center" }}
              >
                <img
                  src={Mail}
                  alt=""
                  style={{
                    objectFit: "contain",
                    height: 70,
                    width: 70,
                    borderRadius: 35,
                  }}
                />
                <div>
                  <h5 style={{ color: "white" }}>{user.email}</h5>
                  <p style={{ color: "white", marginTop: "1em" }}>Email ID</p>
                </div>
              </div>
            </Card.Body>
            <Card.Body
              style={{
                padding: "1em",
                flex: 1,
                borderRadius: "0.5em",
              }}
              background="gradient"
            >
              <div
                style={{ display: "flex", gap: "1em", alignItems: "center" }}
              >
                <img
                  src={Call}
                  alt=""
                  style={{
                    objectFit: "contain",
                    height: 70,
                    width: 70,
                    borderRadius: 35,
                  }}
                />
                <div>
                  <h5 style={{ color: "white" }}>150</h5>
                  <p style={{ color: "white", marginTop: "1em" }}>
                    User Extension
                  </p>
                </div>
              </div>
            </Card.Body>
          </div>
        </div>
      </section>
      <section ref={ref} style={{ minHeight: "80vh", paddingTop: "10vh" }}>
        <FrwdToDevice />
      </section>
      <section style={{ minHeight: "80vh", paddingTop: "10vh" }} ref={availRef}>
        <TimeZoneUser />
      </section>
      <section
        style={{ minHeight: "80vh", paddingTop: "10vh" }}
        ref={allocateRef}
      >
        <div
          style={{
            backgroundColor: "white",
            borderRadius: "1em",
            padding: "1.5em",
            margin: "2em",
          }}
        >
          <h4>Allocate Number</h4>
          <div
            style={{
              display: "flex",
              gap: "1em",
              marginTop: "1em",
              flexWrap: "wrap",
            }}
          >
            {numbers?.map((phone, index) => {
              return (
                <PhoneCard
                  data={phone}
                  index={index}
                  hasTeam={Boolean(phone.teamId)}
                  selected={selectedNumber.includes(phone.id)}
                  onClick={() => {
                    if (selectedNumber.includes(phone.id)) {
                      setSelectedNumber(
                        selectedNumber.filter((n) => n !== phone.id)
                      );
                    } else {
                      setSelectedNumber([...selectedNumber, phone.id]);
                    }
                  }}
                />
              );
            })}
          </div>
          {isChanged && (
            <div
              style={{
                marginTop: "1em",
                display: "flex",
                alignItems: "center",
                gap: "1em",
              }}
            >
              <div>Your changes are not saved</div>
              <Button
                title="Save Changes"
                loading={mutating}
                onClick={() => mutate(selectedNumber)}
              />
            </div>
          )}
        </div>
      </section>
      <section
        style={{ minHeight: "80vh", paddingTop: "10vh" }}
        ref={afterCallRef}
      >
        <AfterCallUser />
      </section>
      <section style={{ minHeight: "80vh", paddingTop: "10vh" }} ref={smsRef}>
        <div style={{ margin: "2em" }}>
          <img
            src={SMS}
            alt=""
            style={{ objectFit: "contain", maxWidth: "100%" }}
          />
        </div>
      </section>
    </div>
  );
}
