import { useRef, useState } from "react";
import { Spinner } from "react-activity";
import { useParams } from "react-router-dom";
import {
  useNumberSettings,
  useSetNumberSettings,
} from "../utils/hooks/useNumberSettings";
import { NumberSettingsAll } from "./types";
import { deepEqual } from "fast-equals";
import { SettingsContext } from "./SettingsContext";
import { CallRecording } from "./components/CallRecording";
import { MusicnMessage } from "./components/MusicnMessage";
import { IVRSection } from "./components/IVRSection";
import { AssignUsers } from "./components/AssignUsers";
import { Extensions } from "./components/Extensions";
import { SaveChanges } from "../components/SaveChanges";

const NumberSettings = () => {
  const { id }: { id: string } = useParams();
  const [setting, setSetting] = useState<NumberSettingsAll>(null!);

  const callRecordRef = useRef<HTMLDivElement>(null);
  const mnmRef = useRef<HTMLDivElement>(null);
  const ivrRef = useRef<HTMLDivElement>(null);
  const allocateRef = useRef<HTMLDivElement>(null);
  const extensionRef = useRef<HTMLDivElement>(null);

  const { isLoading, data } = useNumberSettings(id, setSetting);
  const { mutate, isLoading: mutationLoading } = useSetNumberSettings(
    id,
    setSetting
  );

  const isEqual = deepEqual(data, setting);

  return (
    <SettingsContext.Provider value={{ setting, setSetting }}>
      {isLoading || !setting ? (
        <Spinner />
      ) : (
        <>
          <div style={{ paddingBottom: "5em" }}>
            <div
              style={{
                top: 0,
                left: "1em",
                right: "1em",
                backgroundColor: "#2C3243",
                position: "sticky",
                display: "flex",
                padding: "0.5em 2em",
                justifyContent: "space-around",
                alignItems: "center",
                zIndex: 10,
              }}
            >
              <div
                style={{ padding: "1em", color: "white", cursor: "pointer" }}
                onClick={() =>
                  callRecordRef.current?.scrollIntoView({ behavior: "smooth" })
                }
              >
                Call Recording
              </div>
              <div
                style={{ padding: "1em", color: "white", cursor: "pointer" }}
                onClick={() =>
                  mnmRef.current?.scrollIntoView({ behavior: "smooth" })
                }
              >
                Music and Messages
              </div>
              <div
                style={{ padding: "1em", color: "white", cursor: "pointer" }}
                onClick={() =>
                  ivrRef.current?.scrollIntoView({ behavior: "smooth" })
                }
              >
                IVR
              </div>
              <div
                style={{ padding: "1em", color: "white", cursor: "pointer" }}
                onClick={() =>
                  allocateRef.current?.scrollIntoView({ behavior: "smooth" })
                }
              >
                Allocate Number
              </div>
              <div
                style={{ padding: "1em", color: "white", cursor: "pointer" }}
                onClick={() =>
                  extensionRef.current?.scrollIntoView({ behavior: "smooth" })
                }
              >
                Extension
              </div>
            </div>

            <div ref={callRecordRef}>
              <CallRecording />
            </div>
            <div ref={mnmRef}>
              <MusicnMessage />
            </div>
            <div ref={ivrRef}>
              <IVRSection />
            </div>
            <div ref={allocateRef}>
              <AssignUsers />
            </div>
            <div ref={extensionRef}>
              <Extensions />
            </div>
          </div>
          {!isEqual && (
            <SaveChanges
              isLoading={mutationLoading}
              onSave={() => {
                mutate(setting);
              }}
            />
          )}
        </>
      )}
    </SettingsContext.Provider>
  );
};

export default NumberSettings;
