import { useMutation, useQuery, useQueryClient } from "react-query";
import { CACHE_TIME, UserMe } from ".";
import { axios, getToken } from "../api";
import { Phonenumber, PhoneNumberSettings, Team } from "../types";

export const usePhoneNumber = (id: string | number) => {
  return useQuery(
    ["phonenumber", id],
    () => {
      return axios
        .get(`/api/phonenumber/${id}`, {
          headers: {
            authorization: "Bearer " + getToken(),
          },
        })
        .then(
          (res) =>
            res.data as Phonenumber & {
              users: UserMe[];
              team: Team | null;
              settings: PhoneNumberSettings;
            }
        );
    },
    {
      staleTime: CACHE_TIME,
      cacheTime: CACHE_TIME,
    }
  );
};

type UsersAssign = {
  type: "users";
  ids: number[];
};

type TeamAssign = {
  type: "team";
  id: number;
};

export const useAssignUsers = (id: string | number) => {
  const client = useQueryClient();
  return useMutation(
    (data: UsersAssign | TeamAssign) => {
      return axios.post(`/api/phonenumber/${id}/assign`, data, {
        headers: {
          authorization: "Bearer " + getToken(),
        },
      });
    },
    {
      onSuccess: () => {
        client.invalidateQueries("phonenumber_settings_" + id);
        client.invalidateQueries(["phonenumber", id]);
        client.invalidateQueries("users");
        client.invalidateQueries("phonenumbers");
      },
    }
  );
};
