export const compareNumberArray = (arr1: number[], arr2: number[]) => {
  const len1 = arr1.length;
  const len2 = arr2.length;

  if (len1 !== len2) {
    return false;
  }

  if (arr1 === arr2) {
    return true;
  }

  if (arr1.some((e) => !arr2.includes(e))) {
    return false;
  }

  if (arr2.some((e) => !arr1.includes(e))) {
    return false;
  }

  return true;
};
