import React from "react";
import { Spinner } from "react-activity";
import DataTable from "react-data-table-component";
import { useUserReports } from "../utils/hooks/useReports";

export const ReportsUser = () => {
  const { isLoading, data } = useUserReports();

  if (isLoading || !data) {
    return <Spinner />;
  }

  return (
    <div>
      <DataTable
        customStyles={{
          headRow: {
            style: {
              background: "linear-gradient(90deg, #813866 0%, #ff9d82 100%)",
              borderRadius: 8,
              padding: "0 8px",
            },
          },
          headCells: {
            style: {
              color: "white",
            },
          },
          rows: {
            style: {
              background: "rgb(239, 243, 244)",
              borderRadius: 8,
              margin: "8px 0",
              padding: "0 8px",
              cursor: "pointer",
            },
          },
        }}
        columns={[
          {
            name: "Name",
            selector: (row) => `${row.firstName} ${row.lastName ?? ""}`,
          },
          {
            name: "Total",
            selector: (row) => row.calls.total,
          },
          {
            name: "Missed",
            selector: (row) => row.calls.missed,
          },
          {
            name: "> 30s",
            selector: (row) => row.calls.moreThan30s,
          },
        ]}
        data={data ?? []}
      />
    </div>
  );
};
