import { useState } from "react";
import { Spinner } from "react-activity";
import DataTable from "react-data-table-component";
import ScrollMenu from "react-horizontal-scrolling-menu";
import {
  MdClose,
  MdKeyboardArrowLeft,
  MdKeyboardArrowRight,
} from "react-icons/md";
import ReactModal from "react-modal";
import { useTeams } from "../utils/hooks/useTeams";
import { IVRData } from "../utils/types";
import Button from "./Button";
import { Card } from "./Card/Card";

export default function IVRSettings({
  data,
  setData,
}: {
  data: IVRData["data"];
  setData: (data: IVRData["data"]) => void;
}) {
  const [ivrData, setIvrData] = useState<IVRData["data"]>(() => {
    if (!data) {
      return [];
    }
    return data;
  });
  const { data: teams, isLoading: teamsLoading } = useTeams();
  const [showModal, setShowModal] = useState(false);

  if (!teams || teamsLoading) {
    return <Spinner />;
  }

  const validTeams = teams
    .filter((team) => team.phoneNumber)
    .map(({ id, name }) => ({ id, name }));

  const teamsHash = teams.reduce((prev, { id, name }) => {
    return { ...prev, [id]: name };
  }, {} as { [id: number]: string });

  return (
    <>
      <Card.Body
        background="dark"
        style={{ padding: "1em", borderRadius: "8px", color: "white" }}
      >
        <h4>IVR Settings</h4>
        <div style={{ marginTop: "1em" }}>
          <ScrollMenu
            data={data.map(({ label, id }, index) => {
              return (
                <div style={{ margin: 10 }}>
                  {label && id && (
                    <>
                      <div
                        style={{
                          height: 50,
                          width: 50,
                          borderRadius: 25,
                          backgroundColor: "lightgray",
                          display: "flex",
                          justifyContent: "center",
                          alignItems: "center",
                          margin: 10,
                        }}
                      >
                        <h2 style={{ color: "black" }}>{index + 1}</h2>
                      </div>
                      <div style={{ textAlign: "center" }}>{label}</div>
                    </>
                  )}
                </div>
              );
            })}
            wrapperStyle={{ width: "100%" }}
            arrowLeft={<MdKeyboardArrowLeft size={32} />}
            arrowRight={<MdKeyboardArrowRight size={32} />}
          />
        </div>
        <Button onClick={() => setShowModal(!showModal)}>Edit</Button>
      </Card.Body>
      <ReactModal
        isOpen={showModal}
        style={{
          overlay: {
            zIndex: 10,
          },
        }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            padding: "1em",
          }}
        >
          <h3>IVR Menu</h3>
          <MdClose
            onClick={() => {
              setShowModal(false);
            }}
            style={{ cursor: "pointer" }}
          />
        </div>
        <div style={{ marginTop: "2em", padding: "1em" }}>
          <DataTable
            data={validTeams.map((_, idx) => ({ idx }))}
            columns={[
              {
                name: "Press",
                selector: (row) => row.idx + 1,
              },
              {
                name: "Redirect To",
                cell: (row) => {
                  const { idx } = row;

                  return (
                    <select
                      style={{
                        padding: "0.5em",
                        borderRadius: "8px",
                        outline: "none",
                      }}
                      value={ivrData[idx]?.id ?? "None"}
                      onChange={(e) => {
                        setIvrData((oldData) => {
                          const value = e.target.value;

                          const localData = [...oldData];
                          if (value === "None") {
                            localData[idx] = {
                              id: undefined,
                              label: "",
                            };
                          } else {
                            const id = parseInt(value, 10);
                            localData[idx] = {
                              ...localData[idx],
                              id,
                              label: teamsHash[id],
                            };
                          }
                          return localData;
                        });
                      }}
                    >
                      {teams.map(({ id, name, phoneNumber }) => {
                        const isSelected = ivrData.some(
                          (team) => team.id === id
                        );

                        return (
                          <option
                            key={id}
                            value={id}
                            disabled={!phoneNumber || isSelected}
                          >
                            {name}
                          </option>
                        );
                      })}
                      <option value="None">None</option>
                    </select>
                  );
                },
              },
              {
                name: "Label",
                cell: (row) => (
                  <input
                    type="text"
                    disabled={!ivrData[row.idx]?.id}
                    value={ivrData[row.idx]?.label ?? ""}
                    onChange={(e) =>
                      setIvrData((oldData) => {
                        const local = [...oldData];
                        local[row.idx].label = e.target.value;
                        return local;
                      })
                    }
                    style={{ padding: "0.5em" }}
                    placeholder="Marketing"
                  />
                ),
              },
            ]}
          />
        </div>
        <div style={{ marginTop: "1em" }}>
          <Button
            onClick={() => {
              setShowModal(false);
              setData(ivrData);
            }}
          >
            Save
          </Button>
        </div>
      </ReactModal>
    </>
  );
}
