import React, { useEffect, useState } from "react";
import { Spinner } from "react-activity";
import { useHistory, useParams } from "react-router-dom";
import Button from "../components/Button";
import { Card } from "../components/Card/Card";
import Input from "../components/Input";
import UserCard from "../components/UserCard";
import { useForm } from "../utils/hooks";
import { useCompanyUsers } from "../utils/hooks/useCompanyUsers";
import { usePhoneNumbers } from "../utils/hooks/usePhoneNumbers";
import { useEditTeam, useTeam } from "../utils/hooks/useTeams";
import { Team as TeamType } from "../utils/types";

export const Team = () => {
  const { id } = useParams<{ id: string }>();
  const [form, setForm] = useForm<{
    name: string;
    type: TeamType["callType"] | "";
  }>({
    name: "",
    type: "",
  });
  const [selectedUserIds, setSelectedUserIds] = useState<number[]>([]);
  const [teamNumber, setTeamNumber] = useState("None");

  const { data, isLoading } = useTeam(id);
  const { data: users } = useCompanyUsers();
  const { data: numbers } = usePhoneNumbers();
  const history = useHistory();
  const { mutate, isLoading: mutating } = useEditTeam(id);

  const validNumbers =
    numbers
      ?.filter((number) => number.users.length === 0)
      .filter(
        (number) => number.teamId === data?.id || number.teamId === null
      ) ?? [];

  useEffect(() => {
    if (!data) {
      return;
    }

    setForm("name", data.name);
    setForm("type", data.callType);
    setSelectedUserIds(data.users.map(({ id }) => id));
    setTeamNumber(data.phoneNumber?.id.toString() ?? "None");
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  if (id === "new") {
    return null;
  }

  if (isLoading) {
    return <Spinner />;
  }

  return (
    <div style={{ padding: "2em 1em" }}>
      <Card>
        <Card.Body
          background="light"
          style={{ padding: "2em", borderRadius: 8 }}
        >
          <h3 style={{ color: "white" }}>New Team</h3>
          <div style={{ marginTop: "2em", color: "white" }}>
            Team Name :{" "}
            <Input
              name="name"
              required
              value={form.name}
              onChange={(e) => setForm("name", e.target.value)}
            />
          </div>
          <div style={{ marginTop: "2em", color: "white" }}>
            Cascading Order
          </div>
          <div
            style={{
              marginTop: "1em",
              display: "flex",
              justifyContent: "space-around",
            }}
          >
            <div>
              <label htmlFor="Simultaneously" style={{ color: "white" }}>
                Simultaneously
              </label>
              <input
                type="radio"
                id="Simultaneously"
                value="SIMULTANEOUSLY"
                name="cascading"
                required
                checked={form.type === "SIMULTANEOUSLY"}
                onClick={() => setForm("type", "SIMULTANEOUSLY")}
              />
            </div>
            <div>
              <label htmlFor="FindOrder" style={{ color: "white" }}>
                Find Order
              </label>
              <input
                type="radio"
                id="FindOrder"
                value="FIRST_ORDER"
                name="cascading"
                required
                checked={form.type === "FIRST_ORDER"}
                onClick={() => setForm("type", "FIRST_ORDER")}
              />
            </div>
            <div>
              <label htmlFor="RoundRobin" style={{ color: "white" }}>
                Round Robin
              </label>
              <input
                type="radio"
                id="RoundRobin"
                value="ROUND_ROBIN"
                name="cascading"
                required
                checked={form.type === "ROUND_ROBIN"}
                onClick={() => setForm("type", "ROUND_ROBIN")}
              />
            </div>
          </div>
          <div style={{ marginTop: "2em", color: "white" }}>
            Allocate Members
          </div>
          <div
            style={{
              marginTop: "2em",
              display: "flex",
              flexWrap: "wrap",
              gap: "1em",
            }}
          >
            {users?.map((user) => {
              const inSameTeam = user.team?.id === data?.id;

              return (
                <UserCard
                  key={user.id}
                  name={`${user.firstName} ${user.lastName ?? ""}`}
                  email={user.email}
                  selected={selectedUserIds.includes(user.id)}
                  team={inSameTeam ? null : user.team}
                  onClick={() => {
                    if (selectedUserIds.includes(user.id)) {
                      setSelectedUserIds(
                        selectedUserIds.filter((u) => u !== user.id)
                      );
                    } else {
                      setSelectedUserIds([...selectedUserIds, user.id]);
                    }
                  }}
                />
              );
            })}
          </div>
          <div
            style={{
              display: "flex",
              gap: "1em",
              alignItems: "center",
              marginTop: "2em",
            }}
          >
            <div style={{ color: "white" }}>Assign Phone number</div>
            <div>
              <select
                name="phoneNumber"
                id=""
                style={{
                  padding: "0.5em",
                  outline: "none",
                  borderRadius: "0.25em",
                }}
                value={teamNumber}
                onChange={(e) => setTeamNumber(e.target.value)}
              >
                <option value="None">None</option>
                {validNumbers?.map((number) => {
                  return (
                    <option
                      value={number.id}
                    >{`${number.name} (${number.number})`}</option>
                  );
                })}
              </select>
            </div>
          </div>
        </Card.Body>
      </Card>
      <div
        style={{
          display: "flex",
          gap: "1em",
          marginTop: "1em",
          justifyContent: "flex-end",
        }}
      >
        <Button type="reset" onClick={() => history.push("/teams")}>
          Cancel
        </Button>
        <Button
          type="submit"
          loading={mutating}
          onClick={() => {
            mutate({
              name: form.name,
              type: form.type === "" ? "SIMULTANEOUSLY" : form.type,
              userIds: selectedUserIds,
              phoneNumberId:
                teamNumber === "None" ? null : parseInt(teamNumber, 10),
            });
          }}
        >
          Save
        </Button>
      </div>
    </div>
  );
};
