import React, { useEffect, useMemo, useState } from "react";
import { Spinner } from "react-activity";
import Button from "../../components/Button";
import { Card } from "../../components/Card/Card";
import { compareNumberArray } from "../../utils/general";
import { useCompanyUsers } from "../../utils/hooks/useCompanyUsers";
import {
  useAssignUsers,
  usePhoneNumber,
} from "../../utils/hooks/usePhoneNumber";
import { useSettingsContext } from "../SettingsContext";

export const AssignUsers = () => {
  const { setting } = useSettingsContext();

  const [selectedUsers, setSelectedUsers] = useState<number[]>([]);

  const { data, isLoading } = usePhoneNumber(setting.id);
  const { data: users, isLoading: usersLoading } = useCompanyUsers();

  const { mutate, isLoading: mutating } = useAssignUsers(setting.id);

  useEffect(() => {
    if (!data) {
      return;
    }

    setSelectedUsers(data.users.map((user) => user.id));
  }, [data]);

  const loading = isLoading || usersLoading;

  const isChanged = useMemo(() => {
    const initialUsers = data?.users.map((user) => user.id) ?? [];
    return !compareNumberArray(initialUsers, selectedUsers);
  }, [data, selectedUsers]);

  return (
    <section style={{ paddingTop: "5vh", margin: "2em", marginTop: "8em" }}>
      <Card.Body
        style={{
          padding: "1em",
          borderRadius: "8px",
          border: "2px solid gray",
        }}
      >
        <h3>Assign Number</h3>
        {setting.ivrEnabled ? (
          <p style={{ marginTop: "1em", color: "red" }}>
            IVR is enabled for this number, you cannot assign Users or Team
          </p>
        ) : loading ? (
          <Spinner />
        ) : data?.teamId ? (
          <p style={{ marginTop: "1em", color: "red" }}>
            This number is assigned to a team and can't be assigned to users
          </p>
        ) : (
          <>
            <p style={{ marginTop: "1em" }}>
              Assign this phone number to users
            </p>
            <div style={{ marginTop: "1em" }}>
              {users?.map(({ id, firstName, lastName }) => {
                const isSelected = selectedUsers.includes(id);

                const toggleUser = () => {
                  setSelectedUsers((prevSelectedUsers) =>
                    isSelected
                      ? prevSelectedUsers.filter((u) => u !== id)
                      : [...prevSelectedUsers, id]
                  );
                };

                const htmlId = `${id}_user`;

                return (
                  <div
                    key={id}
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "1em",
                      color: "black",
                    }}
                  >
                    <input
                      type="checkbox"
                      checked={isSelected}
                      onChange={toggleUser}
                      id={htmlId}
                      style={{ width: 20, height: 20 }}
                    />
                    <label htmlFor={htmlId}>
                      {firstName} {lastName}
                    </label>
                  </div>
                );
              })}
            </div>
            {isChanged && (
              <div
                style={{
                  marginTop: "1em",
                  display: "flex",
                  alignItems: "center",
                  gap: "1em",
                }}
              >
                <div>Your changes are not saved</div>
                <Button
                  title="Save Changes"
                  loading={mutating}
                  onClick={() => mutate({ type: "users", ids: selectedUsers })}
                />
              </div>
            )}
          </>
        )}
      </Card.Body>
    </section>
  );
};
