import React from "react";
import Toggle from "react-toggle";
import { Card } from "../../components/Card/Card";
import { useSettingsContext } from "../SettingsContext";
import Music from "../../images/music_messages.png";
import Voicemail from "../../images/Voicemail.png";
import AWSUploader from "../../components/AWSUploader";

export const MusicnMessage = () => {
  const { setting, setSetting } = useSettingsContext();

  return (
    <section style={{ paddingTop: "5vh", margin: "2em", marginTop: "8em" }}>
      <div style={{ marginTop: "8em", display: "flex", gap: "1em" }}>
        <div style={{ flex: 2 }}>
          <Card.Body
            background="light"
            style={{
              padding: "1em",
              borderRadius: "8px",
              color: "white",
              backgroundColor: "#D3617B",
            }}
          >
            <h3>Music and Messages</h3>
            <div
              style={{
                marginTop: "1em",
                gap: "1em",
                display: "flex",
                justifyContent: "space-around",
                alignItems: "center",
                marginBottom: "0.5em",
              }}
            >
              <img src={Music} alt="" style={{ width: 100, height: 100 }} />
              <div>
                <h4>Greeting Message</h4>
                <div
                  style={{
                    display: "flex",
                    gap: "1em",
                    alignItems: "center",
                  }}
                >
                  <p>Welcome users with a greeting.</p>
                  <Toggle
                    icons={false}
                    checked={setting?.greetingMsgStatus !== "DISABLED"}
                    onChange={() => {
                      setSetting((oldSetting) => {
                        if (oldSetting.greetingMsgStatus === "DISABLED") {
                          return {
                            ...oldSetting,
                            greetingMsgStatus: "TEXT",
                          };
                        }
                        return {
                          ...oldSetting,
                          greetingMsgStatus: "DISABLED",
                        };
                      });
                    }}
                  />
                </div>
                <form>
                  <div>
                    <div
                      style={{
                        display: "flex",
                        gap: "0.5em",
                        alignItems: "center",
                      }}
                    >
                      <input
                        type="radio"
                        name="greeting"
                        id="text"
                        disabled={setting?.greetingMsgStatus === "DISABLED"}
                        checked={setting?.greetingMsgStatus === "TEXT"}
                        onClick={() => {
                          setSetting((oldSetting) => {
                            return {
                              ...oldSetting,
                              greetingMsgStatus: "TEXT",
                              greetingMsgText: "",
                            };
                          });
                        }}
                      />
                      <label htmlFor="text">Text</label>
                      {setting?.greetingMsgStatus === "TEXT" && (
                        <input
                          type="text"
                          style={{
                            marginLeft: "0.5em",
                            padding: "0.5em 1em",
                            outline: "none",
                            border: "none",
                            borderRadius: "0.5em",
                          }}
                          value={setting.greetingMsgText}
                          onChange={(e) => {
                            setSetting((oldSetting) => {
                              return {
                                ...oldSetting,
                                greetingMsgText: e.target.value,
                              };
                            });
                          }}
                        />
                      )}
                    </div>
                    <div
                      style={{
                        display: "flex",
                        gap: "0.5em",
                        alignItems: "center",
                        marginTop: "1em",
                      }}
                    >
                      <input
                        type="radio"
                        name="greeting"
                        id="audio"
                        disabled={setting?.greetingMsgStatus === "DISABLED"}
                        checked={setting?.greetingMsgStatus === "AUDIO"}
                        onClick={() => {
                          setSetting((oldSetting) => {
                            return {
                              ...oldSetting,
                              greetingMsgStatus: "AUDIO",
                              greetingMsgAudio: "",
                            };
                          });
                        }}
                      />
                      <label htmlFor="audio">Audio</label>
                      {setting?.greetingMsgStatus === "AUDIO" && (
                        <AWSUploader
                          awsKey={`${setting.id}/greeting`}
                          initialURL={setting.greetingMsgAudio}
                          accept="audio/*"
                          onComplete={(key) => {
                            console.log("Uploaded", key);
                            setSetting((oldSetting) => {
                              return {
                                ...oldSetting,
                                greetingMsgAudio: key,
                              };
                            });
                          }}
                        />
                      )}
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </Card.Body>
        </div>
        <div>
          <Card.Body
            style={{
              padding: "1em",
              height: "85%",
              borderRadius: "8px",
              border: "2px solid gray",
              flex: 1,
            }}
          >
            <h4>Language</h4>
            <select
              name=""
              id=""
              disabled={setting?.greetingMsgStatus !== "TEXT"}
              style={{
                padding: "0.5em",
                backgroundColor: "#6E376E",
                color: "white",
                outline: "none",
                borderRadius: "0.4em",
              }}
            >
              <option value="">English (USA)</option>
              <option value="">English (UK)</option>
              <option value="">English (India)</option>
            </select>
            <div style={{ marginTop: "1.5em" }}>
              <form>
                <div>
                  <div
                    style={{
                      display: "flex",
                      gap: "0.5em",
                      alignItems: "center",
                    }}
                  >
                    <input
                      type="radio"
                      name="greeting"
                      id="male"
                      disabled={setting?.greetingMsgStatus !== "TEXT"}
                    />
                    <label htmlFor="male">Male</label>
                  </div>
                  <div
                    style={{
                      display: "flex",
                      gap: "0.5em",
                      alignItems: "center",
                      marginTop: "1em",
                    }}
                  >
                    <input
                      type="radio"
                      name="greeting"
                      id="audio"
                      disabled={setting?.greetingMsgStatus !== "TEXT"}
                    />
                    <label htmlFor="audio">Female</label>
                  </div>
                </div>
              </form>
            </div>
          </Card.Body>
        </div>
      </div>
      <div style={{ marginTop: "2em" }}>
        <Card.Body
          background="dark"
          style={{
            padding: "1em",
            borderRadius: "8px",
            color: "white",
            paddingBottom: "2em",
          }}
        >
          <h4>Voicemail</h4>

          <div
            style={{
              marginTop: "1em",
              display: "flex",
              gap: "1em",
              marginLeft: "10em",
              justifyContent: "space-around",
              alignItems: "center",
            }}
          >
            <p>Set your voicemail message</p>
            <Toggle
              checked={setting?.voicemailStatus !== "DISABLED"}
              onChange={() => {
                setSetting((oldSetting) => {
                  if (oldSetting.voicemailStatus === "DISABLED") {
                    return {
                      ...oldSetting,
                      voicemailStatus: "TEXT",
                    };
                  }
                  return {
                    ...oldSetting,
                    voicemailStatus: "DISABLED",
                  };
                });
              }}
            />
          </div>
          <div
            style={{
              marginTop: "1em",
              marginLeft: "3em",
              marginBottom: "1em",
              display: "flex",
              gap: "1em",
            }}
          >
            <img
              src={Voicemail}
              alt=""
              style={{ width: 200, objectFit: "contain" }}
            />
            <div style={{ flex: 2, marginLeft: "5em" }}>
              <form>
                <div>
                  <div
                    style={{
                      display: "flex",
                      gap: "0.5em",
                      alignItems: "center",
                    }}
                  >
                    <input
                      type="radio"
                      name="greeting"
                      id="voicemailText"
                      disabled={setting?.voicemailStatus === "DISABLED"}
                      checked={setting?.voicemailStatus === "TEXT"}
                      onClick={() => {
                        setSetting((oldSetting) => {
                          return {
                            ...oldSetting,
                            voicemailStatus: "TEXT",
                            voicemailText: "",
                          };
                        });
                      }}
                    />
                    <label htmlFor="voicemailText">Text</label>
                    {setting?.voicemailStatus === "TEXT" && (
                      <input
                        type="text"
                        style={{
                          marginLeft: "0.5em",
                          padding: "0.5em 1em",
                          outline: "none",
                          border: "none",
                          borderRadius: "0.5em",
                        }}
                        value={setting.voicemailText}
                        onChange={(e) => {
                          setSetting((oldSetting) => {
                            return {
                              ...oldSetting,
                              voicemailText: e.target.value,
                            };
                          });
                        }}
                      />
                    )}
                  </div>
                  <div
                    style={{
                      display: "flex",
                      gap: "0.5em",
                      alignItems: "center",
                      marginTop: "1em",
                    }}
                  >
                    <input
                      type="radio"
                      name="greeting"
                      id="voicemailAudio"
                      disabled={setting?.voicemailStatus === "DISABLED"}
                      checked={setting?.voicemailStatus === "AUDIO"}
                      onClick={() => {
                        setSetting((oldSetting) => {
                          return {
                            ...oldSetting,
                            voicemailStatus: "AUDIO",
                            voicemailAudio: "",
                          };
                        });
                      }}
                    />
                    <label htmlFor="voicemailAudio">Audio</label>
                    {setting?.voicemailStatus === "AUDIO" && (
                      <AWSUploader
                        awsKey={`${setting.id}/voicemail`}
                        initialURL={setting.voicemailAudio}
                        accept="audio/*"
                        onComplete={(key) => {
                          setSetting((oldSetting) => {
                            return {
                              ...oldSetting,
                              voicemailAudio: key,
                            };
                          });
                        }}
                      />
                    )}
                  </div>
                </div>
              </form>
            </div>
          </div>
        </Card.Body>
      </div>
    </section>
  );
};
