import React from "react";
import Toggle from "react-toggle";
import { Card } from "../../components/Card/Card";
import { useSettingsContext } from "../SettingsContext";

export const CallRecording = () => {
  const { setting, setSetting } = useSettingsContext();

  return (
    <section
      style={{
        paddingTop: "5vh",
        display: "flex",
        gap: "1em",
        margin: "2em",
      }}
    >
      <div style={{ flex: 1 }}>
        <div>
          <Card.Body
            background="gradient"
            style={{
              padding: "1em",
              borderRadius: "8px",
              color: "white",
            }}
          >
            <h3>Record Calls</h3>
            <div style={{ display: "flex", gap: "3em", padding: "3em 0.75em" }}>
              <img
                src="https://play-lh.googleusercontent.com/tTwEQlDdhoklHXZyvNqTZ8JrSSZbm9YuDC7L57wGnYt35GJsKNR6sQ9MxtXECyXZNwUZ"
                alt=""
                style={{
                  width: 100,
                  height: 100,
                  flex: 1,
                }}
              />
              <div>
                <p>
                  Automatically record all the calls. Please check legality in
                  your region
                </p>
                <Toggle
                  checked={setting?.allowRecord}
                  onChange={() => {
                    setSetting((oldSetting) => {
                      return {
                        ...oldSetting,
                        allowRecord: !oldSetting?.allowRecord,
                      };
                    });
                  }}
                />
              </div>
            </div>
          </Card.Body>
        </div>
        <div>
          <Card.Body
            background="orange"
            style={{
              padding: "1em",
              borderRadius: "8px",
              marginTop: "1em",
              color: "white",
            }}
          >
            <h3>Grant Recording access to Users</h3>
            <p style={{ marginTop: "2em" }}>
              Allow users to pause or resume the recording of call. This
              prevents recording of user's sensitive information whenever he
              wants
            </p>
            <Toggle
              checked={setting?.allowPause}
              onChange={() => {
                setSetting((oldSetting) => {
                  return {
                    ...oldSetting,
                    allowPause: !oldSetting?.allowPause,
                  };
                });
              }}
            />
          </Card.Body>
        </div>
      </div>
      <div style={{ flex: 1 }}>
        <Card.Body
          background="dark"
          style={{
            height: "90%",
            padding: "1em",
            borderRadius: "8px",
            color: "white",
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
          }}
        >
          <h3>Number Confirmation</h3>
          <p>Verification is compulsory. Submit address proof if required</p>
          <div
            style={{
              backgroundColor: "#6E376E",
              color: "white",
              padding: "1em",
              borderRadius: "0.4em",
              fontWeight: "bold",

              marginRight: "auto",
              marginLeft: "auto",
            }}
          >
            {setting?.documentStatus}
          </div>
        </Card.Body>
      </div>
    </section>
  );
};
