import { useMutation, useQuery, useQueryClient } from "react-query";
import { CACHE_TIME, UserSettings } from ".";
import { axios, getToken } from "../api";
import { Phonenumber, Team, User } from "../types";

export const useUser = (id: string | number) => {
  return useQuery(
    ["user", id],
    () => {
      if (id !== "new")
        return axios
          .get("/api/admin/user/" + id, {
            headers: {
              Authorization: "Bearer " + getToken(),
            },
          })
          .then(
            (data) =>
              data.data as User & {
                settings: UserSettings;
                phoneNumbers: Phonenumber[];
                team: Team;
              }
          );
    },
    {
      staleTime: CACHE_TIME,
      cacheTime: CACHE_TIME,
    }
  );
};

export const useAssignUserNumber = (id: string | number) => {
  const client = useQueryClient();
  return useMutation(
    (ids: number[]) => {
      return axios.post(
        `/api/admin/user/${id}/assign`,
        { ids },
        {
          headers: {
            Authorization: "Bearer " + getToken(),
          },
        }
      );
    },
    {
      onSuccess: () => {
        client.invalidateQueries(["user", id]);
        client.invalidateQueries("users");
        client.invalidateQueries("phonenumbers");
        client.invalidateQueries("me");
      },
    }
  );
};
