import { useMutation, useQuery, useQueryClient } from "react-query";
import { CACHE_TIME, UserMe } from ".";
import { axios, getToken } from "../api";
import { Phonenumber, Team, User } from "../types";

type TeamAPIReturn = Array<
  Team & { users: UserMe[]; phoneNumber: Phonenumber | null }
>;

export const useTeams = () => {
  return useQuery(
    "teams",
    async () => {
      const data = await axios.get("/api/teams", {
        headers: { authorization: "Bearer " + getToken() },
      });
      return data.data as TeamAPIReturn;
    },
    {
      staleTime: CACHE_TIME,
      cacheTime: CACHE_TIME,
    }
  );
};

export const useDeleteTeams = () => {
  const client = useQueryClient();

  return useMutation(
    async (id: number) => {
      await axios.post(
        "/api/teams/delete",
        { id },
        {
          headers: { authorization: "Bearer " + getToken() },
        }
      );
    },
    {
      onMutate: (id) => {
        const oldTeams = client.getQueryData("teams") as TeamAPIReturn;
        const newTeams = oldTeams.filter((team) => team.id !== id);
        client.setQueryData("teams", newTeams);
        return {
          oldTeams,
        };
      },
      onError: (_, id, context: { oldTeams: TeamAPIReturn } | undefined) => {
        if (!context) {
          return;
        }
        const { oldTeams } = context;
        client.setQueryData("teams", oldTeams);
      },
    }
  );
};

export const useTeam = (id: string | number) => {
  return useQuery(
    ["teams", id],
    () => {
      if (id === "new") return;

      return axios
        .get("/api/teams/" + id, {
          headers: {
            authorization: "Bearer " + getToken(),
          },
        })
        .then(
          (data) =>
            data.data as Team & {
              users: User[];
              phoneNumber: Phonenumber | null;
            }
        );
    },
    {
      cacheTime: CACHE_TIME,
      staleTime: CACHE_TIME,
    }
  );
};

export const useEditTeam = (id: string | number) => {
  type MutationData = {
    name: string;
    phoneNumberId: null | number;
    userIds: number[];
    type: Team["callType"];
  };
  const client = useQueryClient();
  return useMutation(
    (data: MutationData) => {
      return axios.post("/api/teams/" + id, data, {
        headers: {
          authorization: "Bearer " + getToken(),
        },
      });
    },
    {
      onSuccess: () => {
        client.invalidateQueries(["teams", id]);
      },
    }
  );
};
