import React from "react";
import Button from "./Button";

export const SaveChanges = ({ onSave, isLoading }: Props) => {
  return (
    <div
      style={{
        bottom: 0,
        left: 0,
        backgroundColor: "#2C3243",
        position: "sticky",
        display: "flex",
        flexDirection: "row",
        padding: "1em 2em",
        gap: "1em",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <div style={{ color: "white", cursor: "pointer" }}>
        Your changes are not saved
      </div>

      <Button
        loading={isLoading}
        title={isLoading ? "Saving..." : "Save Changes"}
        onClick={isLoading ? undefined : onSave}
      />
    </div>
  );
};

interface Props {
  onSave: () => void;
  isLoading?: boolean;
}
