import React from "react";
import { Spinner } from "react-activity";
import DataTable from "react-data-table-component";
import { useDailyReports } from "../utils/hooks/useReports";

export const ReportsDaily = () => {
  const { isLoading, data } = useDailyReports();

  if (isLoading || !data) {
    return <Spinner />;
  }

  return (
    <div>
      <DataTable
        customStyles={{
          headRow: {
            style: {
              background: "linear-gradient(90deg, #813866 0%, #ff9d82 100%)",
              borderRadius: 8,
              padding: "0 8px",
            },
          },
          headCells: {
            style: {
              color: "white",
            },
          },
          rows: {
            style: {
              background: "rgb(239, 243, 244)",
              borderRadius: 8,
              margin: "8px 0",
              padding: "0 8px",
              cursor: "pointer",
            },
          },
        }}
        columns={[
          {
            name: "Date",
            selector: (row) => row.date,
          },
          {
            name: "Incoming",
            selector: (row) => row.incoming,
          },
          {
            name: "Missed",
            selector: (row) => row.missed,
          },
          {
            name: "Outgoing",
            selector: (row) => row.outgoing,
          },
        ]}
        data={data ?? []}
      />
    </div>
  );
};
