import React from "react";
import { useSettingsContext } from "../SettingsContext";
import IVRImage from "../../images/IVR_Image.png";
import AWSUploader from "../../components/AWSUploader";
import { Card } from "../../components/Card/Card";
import Toggle from "react-toggle";
import IVRSettings from "../../components/IVRSettings";

export const IVRSection = () => {
  const { setting, setSetting } = useSettingsContext();
  const id = setting.id;

  const { ivrData } = setting;

  return (
    <section style={{ paddingTop: "5vh", margin: "2em", marginTop: "8em" }}>
      <div style={{ marginTop: "8em", display: "flex", gap: "1em" }}>
        <div style={{ flex: 2 }}>
          <Card.Body
            background="light"
            style={{
              padding: "1em",
              borderRadius: "8px",
              color: "white",
            }}
          >
            <h3>IVR</h3>
            <div
              style={{
                marginTop: "1em",
                gap: "1em",
                display: "flex",
                justifyContent: "space-around",
                alignItems: "center",
                marginBottom: "0.5em",
              }}
            >
              <img
                src={IVRImage}
                alt=""
                style={{ width: 150, height: 150, objectFit: "contain" }}
              />
              <div>
                <div
                  style={{
                    display: "flex",
                    gap: "1em",
                    alignItems: "center",
                  }}
                >
                  <h4>IVR Message</h4>
                  <Toggle
                    icons={false}
                    checked={setting?.ivrEnabled}
                    onChange={() => {
                      setSetting((oldSetting) => {
                        if (oldSetting.ivrEnabled) {
                          return {
                            ...oldSetting,
                            ivrEnabled: false,
                          };
                        }
                        return {
                          ...oldSetting,
                          ivrEnabled: true,
                          ivrData: {
                            type: "TEXT",
                            data: [],
                            message: "",
                          },
                        };
                      });
                    }}
                  />
                </div>
                <form>
                  <div>
                    <div
                      style={{
                        display: "flex",
                        gap: "0.5em",
                        alignItems: "center",
                      }}
                    >
                      <input
                        type="radio"
                        name="greeting"
                        id="ivrTEXT"
                        disabled={!setting?.ivrEnabled}
                        checked={ivrData.type === "TEXT"}
                        onClick={() => {
                          setSetting((oldSetting) => {
                            return {
                              ...oldSetting,
                              ivrData: {
                                type: "TEXT",
                                message: "",
                                data: oldSetting.ivrData.data,
                              },
                            };
                          });
                        }}
                      />
                      <label htmlFor="ivrTEXT">Text</label>
                      {ivrData.type === "TEXT" && (
                        <input
                          type="text"
                          style={{
                            marginLeft: "0.5em",
                            padding: "0.5em 1em",
                            outline: "none",
                            border: "none",
                            borderRadius: "0.5em",
                          }}
                          value={ivrData.message}
                          onChange={(e) => {
                            setSetting((oldSetting) => {
                              return {
                                ...oldSetting,
                                ivrData: {
                                  type: "TEXT",
                                  message: e.target.value,
                                  data: oldSetting.ivrData.data,
                                },
                              };
                            });
                          }}
                        />
                      )}
                    </div>
                    <div
                      style={{
                        display: "flex",
                        gap: "0.5em",
                        alignItems: "center",
                        marginTop: "1em",
                      }}
                    >
                      <input
                        type="radio"
                        name="greeting"
                        id="ivrAUDIO"
                        disabled={!setting?.ivrEnabled}
                        checked={ivrData.type === "AUDIO"}
                        onClick={() => {
                          setSetting((oldSetting) => {
                            return {
                              ...oldSetting,
                              ivrData: {
                                type: "AUDIO",
                                message: "",
                                data: oldSetting.ivrData.data,
                              },
                            };
                          });
                        }}
                      />
                      <label htmlFor="ivrAUDIO">Audio</label>
                      {ivrData.type === "AUDIO" && (
                        <AWSUploader
                          awsKey={`${id}/ivr`}
                          initialURL={ivrData.message}
                          accept="audio/*"
                          onComplete={(key) => {
                            setSetting((oldSetting) => {
                              return {
                                ...oldSetting,
                                ivrData: {
                                  type: "AUDIO",
                                  message: key,
                                  data: oldSetting.ivrData.data,
                                },
                              };
                            });
                          }}
                        />
                      )}
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </Card.Body>
        </div>
        <div>
          <Card.Body
            style={{
              padding: "1em",
              height: "85%",
              borderRadius: "8px",
              border: "2px solid gray",
              flex: 1,
            }}
          >
            <h4>Language</h4>
            <select
              name=""
              id=""
              disabled={!setting.ivrEnabled}
              style={{
                padding: "0.5em",
                backgroundColor: "#6E376E",
                color: "white",
                outline: "none",
                borderRadius: "0.4em",
              }}
            >
              <option value="">English (USA)</option>
              <option value="">English (UK)</option>
              <option value="">English (India)</option>
            </select>
            <div style={{ marginTop: "1.5em" }}>
              <form>
                <div>
                  <div
                    style={{
                      display: "flex",
                      gap: "0.5em",
                      alignItems: "center",
                    }}
                  >
                    <input
                      type="radio"
                      name="greeting"
                      id="male"
                      disabled={setting.ivrEnabled}
                    />
                    <label htmlFor="male">Male</label>
                  </div>
                  <div
                    style={{
                      display: "flex",
                      gap: "0.5em",
                      alignItems: "center",
                      marginTop: "1em",
                    }}
                  >
                    <input
                      type="radio"
                      name="greeting"
                      id="audio"
                      disabled={setting.ivrEnabled}
                    />
                    <label htmlFor="audio">Female</label>
                  </div>
                </div>
              </form>
            </div>
          </Card.Body>
        </div>
      </div>
      <div style={{ marginTop: "2em" }}>
        {setting.ivrEnabled && (
          <IVRSettings
            data={ivrData.data}
            setData={(newData) => {
              setSetting((oldSettings) => {
                return {
                  ...oldSettings,
                  ivrData: {
                    ...oldSettings.ivrData,
                    data: newData,
                  },
                };
              });
            }}
          />
        )}
      </div>
    </section>
  );
};
