import { AxiosResponse } from "axios";
import { useMutation, useQuery, useQueryClient } from "react-query";
import { CACHE_TIME } from ".";
import { NumberSettingsAll } from "../../PhoneSettings/types";
import { axios, getToken } from "../api";

export const useNumberSettings = (
  id: string | number,
  onSuccess?: (data: NumberSettingsAll) => void
) => {
  return useQuery(
    "phonenumber_settings_" + id,
    async () => {
      const token = getToken();
      const data = await axios.get(`/api/phonenumber/${id}/settings/`, {
        headers: {
          authorization: "Bearer " + token,
        },
      });
      return data.data as NumberSettingsAll;
    },
    {
      staleTime: CACHE_TIME,
      cacheTime: CACHE_TIME,
      onSuccess,
    }
  );
};

export const useSetNumberSettings = (
  id: string | number,
  onSuccess?: (data: NumberSettingsAll) => void
) => {
  const client = useQueryClient();
  return useMutation(
    async (data: NumberSettingsAll) => {
      const { phoneNumber, ...settings } = data;
      const token = getToken();
      const response: AxiosResponse<NumberSettingsAll> = await axios.put(
        `/api/phonenumber/${id}/settings/`,
        settings,
        {
          headers: {
            authorization: "Bearer " + token,
          },
        }
      );
      return response.data;
    },
    {
      onSuccess: (data) => {
        client.setQueryData("phonenumber_settings_" + id, data);
        client.invalidateQueries(["phonenumber", id]);
        client.invalidateQueries("phonenumber_settings_" + id);
        onSuccess?.(data);
      },
    }
  );
};
